<script lang="ts" setup>
import Language from '@/components/Language.vue'
import Avatar from '@/components/Avatar.vue'
import { ElDropdown, ElDropdownMenu, ElDropdownItem } from 'element-plus'
import { useI18n } from 'vue-i18n'
import { useUserInfoStore } from '@/store/user-data'
import { useRouter } from 'vue-router'
const { t } = useI18n()
const router = useRouter()
const userInfoStore = useUserInfoStore()

const logout = () => {
  userInfoStore.handleLogout()
  router.push('login')
}

const handleSetting = () => {
  router.push('setting')
}
</script>
<template>
  <div class="header-right">
    <Language />
    <el-dropdown v-if="userInfoStore.isLogined">
      <Avatar class="ml16" />
      <template #dropdown>
        <el-dropdown-menu>
          <el-dropdown-item @click="handleSetting">{{ t('setting') }}</el-dropdown-item>
          <el-dropdown-item @click="logout">{{ t('login.logout') }}</el-dropdown-item>
        </el-dropdown-menu>
      </template>
    </el-dropdown>
  </div>
</template>
<stype lang="scss" scoped>
.header-right {
  float: right;
  height: 100%;
  padding: 0 20px;
  display: flex;
  justify-content: center;
  align-items: center;
}
</stype>
